.nav{height:80px;width:100%;display:flex;padding:0 10%;top:0}.nav-container{width:100%;display:flex;justify-content:space-evenly;align-items:center}.nav .logo{width:10rem}nav ul{padding:0}nav ul li{display:inline-block;line-height:80px;list-style:none}nav ul li a{color:var(--main-font-color);font-size:17px;padding:7px 13px;border-radius:3px;font-weight:normal}.checkbtn{font-size:30px;color:#212121;float:left;line-height:80px;cursor:pointer;display:none}nav input{display:none}.fa-times{display:none !important}.contact-container{display:flex;flex-wrap:nowrap;align-items:center;justify-content:flex-end;width:100%}.contact{padding:15px 20px;color:red;background-color:white;border-radius:3px;border:none;box-shadow:3px 0px 10px 0px rgba(0,0,0,0.2);font-size:13px;padding-right:35px;cursor:pointer}.contact:hover i{opacity:0;padding-left:20px}.contact i{top:34px;font-weight:20px;font-size:15px;position:absolute;padding-left:10px;transition:1s ease-out}.menu{display:flex;width:100%;height:100%;justify-content:space-evenly;align-items:center;position:relative;left:0}@media (max-width: 858px){.checkbtn{display:block;max-width:20px !important}nav{position:fixed;z-index:1500;background-color:white;padding:0 !important}.nav::before{content:"";position:fixed;width:100%;height:calc(100vh - 80px);bottom:0;background-color:rgba(0,0,0,0.8);opacity:0;-webkit-transform:scale(0);transform:scale(0);transition:opacity 0.5s}.open .fa-times{display:inline-block !important}.open .fa-bars{display:none}.open .nav::before{opacity:1;-webkit-transform:scale(1);transform:scale(1);border-bottom:1px solid var(--main-font-color)}.open nav ul{left:0}.nav ul{position:fixed;width:80%;height:100vh;background:white;top:80px;left:-100%;text-align:center;transition:0.5s;z-index:1200;margin:0}nav ul li{display:block;margin:50px 0;line-height:30px}nav ul li a{font-size:20px}.menu{justify-content:space-between;padding-left:7%}.contact{padding:10px 15px;margin-right:10%;width:130px;font-size:12px}.contact i{display:none}}

.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.7);z-index:1500}.modal .form{margin:0}.modal .modal-main{position:fixed;background:white;width:60%;height:auto;padding:30px;top:50%;left:50%;border-radius:20px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.modal .modal-main .fa-times{display:block !important;position:absolute;right:40px;top:60px;font-size:20px;cursor:pointer}@media (max-width: 858px){.modal .modal-main{width:80%}}.modal .modal-main .name-email{flex-direction:column}.modal .modal-main .name-email .group{margin:0 !important}.modal .modal-main .name-email .group:nth-child(1) .form-input{margin-bottom:5px}.modal .modal-main .name-email .form-input{margin:25px 0}.display-block{display:block}.display-none{display:none}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:#F4FAFE;border-radius:5px;color:#314584;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-16px;font-size:15px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:#314584;font-size:20px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-16px;font-size:15px;color:#000}

.button{padding:10px 20px;background-color:red;color:white;border:none;border-radius:25px;box-shadow:0px 5px 10px 0px rgba(255,57,70,0.15);transition:1s;font-size:15px;font-weight:600}.button:hover{padding:12px 25px}

.form{width:100%;margin:10% 0}.form h3{font-size:46px;margin:10px 0;color:#314584}.form .group:nth-child(3){margin:10px 0}.form .group:nth-child(3) .form-input{margin:0px;height:100px}.form .name-email{width:100%;display:flex;justify-content:center;align-items:center}.form .name-email .group{width:100%;display:block;position:relative;bottom:0;height:100%;margin:0}.form .name-email .group:nth-child(1){margin-right:2%}.form .name-email .group .form-input:focus ~ .form-input-label{top:4px;font-size:15px;color:black}.form .name-email .group .form-input-label{top:35px}.form .name-email .group .form-input-label.shrink{top:4px;font-size:15px;color:black}.form .button{display:block;margin:30px auto}@media (max-width: 580px){.name-email{flex-direction:column}.name-email .form-input{margin:25px 0}.group:nth-child(3){margin:25px 0 !important}}

.services-container{width:100%;display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;margin:10% 0}.services-container .shape{position:absolute;z-index:-1;left:0;width:100%;max-width:278px;height:auto}.services-container .items{display:flex;flex-wrap:wrap}@media (max-width: 1100px){.services-container .items{width:100%;justify-content:space-around;margin:0}}.services-container .action{width:33%}@media (max-width: 1100px){.services-container .action{width:100%;text-align:center;margin:3em 0}}.services-container .action h2{font-size:45px}

.item-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-start;width:100%;height:360px;max-width:260px;margin:10px 15px;padding:30px;background-color:white;box-shadow:0px 5px 10px 0px rgba(25,45,51,0.15);word-wrap:break-word}.item-container .image{width:60px}.item-container p{width:100%}.item-container h3{font-size:27px;color:#314584;width:100%}

.intro-container{display:flex;flex-direction:row;height:calc(100vh - 80px);padding:2em 0;max-width:2000px;margin:0 auto}.intro-text{width:49%;display:flex;flex-direction:column;justify-content:center;height:70%;padding-left:10%}.intro-text h1{color:#314584;font-size:3em}.intro-text p{font-size:1.1em;font-weight:300;line-height:1.7em;margin-bottom:2em}.intro-image{width:51%;margin-left:2em}.intro-image .shape{width:100%;height:auto;max-width:680px;margin-right:0;float:right}.intro-image .shape2{position:absolute;z-index:-1;right:0;width:50%;height:auto;max-width:820px}.unstyled-button{padding:10px 20px;background-color:white;color:#314584;border:none;border-radius:25px;box-shadow:0px 5px 10px 0px rgba(255,57,70,0.15);font-size:1em;margin:0 1em;font-weight:600;border:1px solid #fff}.unstyled-button:hover{border:1px solid #314584}@media (max-width: 858px){.intro-container{padding-top:6em}}@media (max-width: 830px){.intro-container{flex-direction:column;text-align:center;height:100%}.intro-text{width:100%;padding:0 10%}.intro-image{width:100%;margin:10% 0;padding:0 5%}.shape2{width:100% !important}}@media (max-width: 375px){.unstyled-button{margin-top:1em}}

.about{display:flex;flex-direction:row;margin:10% 0;width:100%}.about .shape{position:absolute;z-index:-1;right:0;width:100%;max-width:278px;height:auto}.about .about-image{width:48%;margin:2%}.about .about-image .ab-img{width:100%;height:auto}.about .about-text{width:48%;margin:2%}.about .about-text span{color:red;font-size:.9em}.about .about-text h1{color:#314584;font-weight:600}.about .about-text p{font-size:.9em;line-height:1.5em}@media (max-width: 858px){.about{flex-direction:column;text-align:center}.about .about-image{width:100%}.about .about-text{width:100%;margin:auto;text-align:center}}

.portofolio-container{width:100%;color:#314584;display:flex;flex-direction:column;margin:15% 0}.portofolio-container .shape{position:absolute;z-index:-1;left:0;width:100%;max-width:678px;height:auto}.portofolio-container span{text-align:center;color:red;cursor:pointer;font-weight:500}.portofolio-container .portofolio-text h1{font-weight:600;margin:0}

.portofolio-images .portofolio-row{display:flex;justify-content:space-around;width:100%}.portofolio-images .portofolio-row img{width:100%;height:auto;min-width:200px;margin:1em 1em;cursor:pointer}.portofolio-images .portofolio-row img:hover{opacity:.5}@media (max-width: 858px){.portofolio-images{margin:0}.portofolio-images .portofolio-row{flex-wrap:wrap}.portofolio-images .portofolio-row img{width:100%;height:auto;background:blue}}

.news-container{margin:15% 0;display:flex;flex-direction:column}.news-container .shape{position:absolute;z-index:-1;right:0;width:100%;height:auto;max-width:350px}.news-container .news-text span{color:red;font-weight:500}.news-container .news-text h1{color:#314584;font-weight:600}.news-container .news-cards{display:flex;flex-direction:row;justify-content:space-between}.news-container .news-cards .active{background:#FF3E4D !important;color:white !important}.news-container .news-cards .card{color:#314584;box-shadow:0px 5px 10px 0px rgba(25,45,51,0.15);width:40%;height:auto;max-width:370px;min-height:350px;margin:0 1em;cursor:pointer;background-color:white}.news-container .news-cards .card:hover{background:#FF3E4D;color:white}.news-container .news-cards .card .card-content{margin:1em 2em}.news-container .news-cards .card .card-content p{line-height:1.3em}@media (max-width: 858px){.news-container .news-cards{flex-direction:column}.news-container .news-cards .card{margin:1em auto;width:100%}}

.footer{position:relative;flex-shrink:0;bottom:0;height:100%;padding:3% 0;background-color:#314584;display:flex;flex-direction:column;width:100%;justify-content:center;align-items:center;color:white}.footer h3{color:#FF3946;font-size:40px;margin:10px}.footer ul{display:flex;list-style:none;padding:0}.footer ul li{margin:0 10px}.footer a{margin:10px 30px;font-size:40px;color:white}

.container{padding:0 10%;max-width:2000px;margin:0 auto}

*{
  text-decoration: none;
  box-sizing: border-box;
}
html{
  height: 100%;
}
body{
  font-family:'Work Sans', sans-serif;
  min-height: 100%;
  top: 0;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
#root{
  min-height: 100vh;
  top:0;
}
.App{
  max-width: 1800px;
  margin: 0 auto;
}
.portofolio-page .buttons{display:none}.portofolio-page .web-container{display:flex;justify-content:space-around;flex-wrap:wrap;padding:0 10%}.portofolio-page .design{padding:0 10%}

.switch{width:100%;display:flex;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}.switch h1{font-size:45px;border-bottom:5px solid rgba(254,182,22,0.5);transition:0.5s;cursor:pointer}.switch-active{color:black;border-bottom:5px solid #feb616 !important}


.scroll-img{
    margin:20px;
      width: 250px;
      height:400px;
    box-shadow: 0 20px 30px rgba(0, 0,0, 0.5);
    background-size: cover!important;
    transition: 5s;
    background-position: top!important;
    
}
.scroll-img:hover{
    background-position: bottom!important;
}


.scroll-img .container{
  width:250px;
  height: 40px;
  background: rgba(0, 0, 0, 0);
  transition: ease-in-out 0.5s;
}
.scroll-img .container p{
  margin:0;
  padding:0;
  text-align: center;
  color: rgba(0, 0, 0, 0);
  font-size: 2rem;
  font-family: sans-serif, arial;
}
a{
  text-decoration: none;
}
.scroll-img:hover .container{
  background: rgba(255, 255, 255, 0.8);
}
.scroll-img:hover .container p{
  color: black;
}
.aboutus-page{width:80%;margin:5em auto;font-size:1.1em}

